<template>
  <div class="home" ref="home">
    <div class="triangle-box">
      <div class="triangle"></div>
      <div class="blue-bg"></div>
    </div>
    <div class="wrapper">
      <div class="shoes">
         <canvas ref="comCanvas" style="width:100px; height:100px"></canvas>
      </div>
      <div class="main-title"><img src="../assets/images/main-title.png"/></div>
      <div class="sub-title"><img src="../assets/images/sub-title.png"/></div>
      <div class="art-title"><img src="../assets/images/art-title.png"/></div>
    </div>
    <Footer :isFixed="isFixed"></Footer>  
  </div>
</template>

<script>
import Footer from '@/components/footer/footer.vue'
import * as THREE from 'three';
import GLTFLoader from '../assets/js/GLTFLoader';
// import Stats from 'stats.js';
export default {
  components:{
    Footer
  },
  data: function() {
    return {
      percent:'100%',
      isFixed:false
    };
  },
  mounted: function() {
    var comCanvas = this.$refs.comCanvas;
    // 渲染完执行的函数
    /**
     * 创建场景对象Scene
     */
    var scene = new THREE.Scene();  
    // 调试用，显示一秒渲染的次数
    // var stats = new Stats();
    // document.body.appendChild(stats.dom);
    /*
    加载GLTF格式的模型文件
    */
    // 实例化一个gltf加载器
    var loader = new GLTFLoader();
    // 指定加载的文件地址和加载函数
    loader.load('/static/model/23-AGWN041-1.gltf', function(obj) {
      console.log(`1:obj`);
      obj.scene.children[0].position.set(0, -1, 0);
      obj.scene.name = 'root';
      obj.scene.scale.set(2.5, 2.5, 2.5);
      scene.add(obj.scene);
    }, function(xhr) {
      console.log((xhr.loaded / xhr.total * 100) + '%loaded');
    }, function(error) {
      // 如果加载错误，显示报错！以及错误信息
      console.log('Error!', error);
    });

    /**
     * 光源设置
     */
    // 环境光
    var ambient = new THREE.AmbientLight(0x555555);
    ambient.intensity = 0.5;
    scene.add(ambient);
    console.log(scene);
    // canvas的原始大小（背景框）
    var canvasW = 850;
    var canvasH = 850;
    
    /**
     * 相机设置:透视投影
     */
    // var camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.1, 10000);
    var camera = new THREE.PerspectiveCamera(45, canvasW / canvasH, 0.1, 10000);
    camera.position.set(0, 0, 40);
    camera.lookAt(scene.position);
    /**
     * 创建渲染器对象
     */
    var renderer = new THREE.WebGLRenderer({canvas: comCanvas,alpha:true});
    render.alpha = 0;
    // renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.setSize(canvasW, canvasH);
    // renderer.setClearColor(0xDBDBDB, 1);// 设置背景颜色
    renderer.gammaInput = true;// 输出时打开gamma
    renderer.gammaOutput = true;// 输出时打开gamma
    
    // 渲染函数
    function render() {
      renderer.render(scene, camera); // 执行渲染操作
      // stats.update();
      scene.traverse(function(e) {
        if (e.name === 'root') {
          e.rotation.y += 0.01;
        }
      });
      requestAnimationFrame(render); // 请求再次执行渲染函数render，渲染下一帧
      comCanvas.style.width = '100%'
      comCanvas.style.height = '180%'
    }
    render();

    const h = document.documentElement.clientHeight || document.body.clientHeight;
    const w = document.documentElement.clientWidth || document.body.clientWidth;
    let height = 0;
    if(w <= 768){
      height= this.$refs.home.offsetHeight + 200;
    }else{
      height= this.$refs.home.offsetHeight;
    }

    if(height > h){
      this.isFixed = false
    }else{
      this.isFixed = true
    }
  },

};
</script>

