<template>
  <div class="introduce-wrapper" ref="introduce">
    <Title :titleImg="img" englistTitle="Product introduction" />
    <div class="model-list" v-for="item in modelList" :key="item.index">
      <Model 
      :mainTitle="item.mainTitle" 
      :explain="item.explain"
      :num="item.index"
      :img="item.img"
      :hover="item.hover"
      :circleColor="item.circleColor"
      :isMobile="isMobile"
      :mobileBg="item.hover"
      :mobileCircle="item.circleColor"
      />
    </div>
    <Footer :isFixed="isFixed"></Footer>  
  </div>
</template>

<script>
import Footer from '@/components/footer/footer.vue'
import Title from '@/components/title/title.vue'
import Model from '@/components/model/model.vue'
export default {
  components:{
    Title,Model,Footer
  },
  data(){
    return{
      isFixed:false,
      isMobile:false,
      img:require('../assets/images/introduce-title.png'),
      modelList:[{
        mainTitle:'3D模型',
        explain:'让鞋款在小程序上能直接用3d展示，可以将鞋子360度旋转 省去更多的主图效果，点击3D空间即可体验更有趣的购鞋交互体验。3d模型在移动端展示是未来企业为客户提供最好的物品展示方式，这对企业在未来市场竞争至关重要。',
        index:"01",
        img:'',
        hover:'#09D293',
        circleColor:'#1A09D5'
      },{
        mainTitle:'AR展陈',
        explain:'通过ar增强现实技术与智能手机的结合，运用小程序帮助你看到鞋款’穿’在脚上效果，可以模拟出鞋子在光线不同的各种效果。可以增强用户网上购买鞋子更好的用户体验，解决用户经常收到鞋发现适合自己而退货的问题。',
        index:"02",
        img:'',
        hover:'#0DC7D8',
        circleColor:'#2AFFEA'
      },{
        mainTitle:'AR试穿',
        explain:'通过ar增强现实技术与智能手机的结合，运用小程序帮助你看到鞋款’穿’在脚上效果，可以模拟出鞋子在光线不同的各种效果。可以增强用户网上购买鞋子更好的用户体验，解决用户经常收到鞋发现适合自己而退货的问题。',
        index:"03",
        img:'',
        hover:'#1866DB',
        circleColor:'#43FFC4'
      }]
    }
  },
  mounted() {
    const h = document.documentElement.clientHeight || document.body.clientHeight;
    const w = document.documentElement.clientWidth || document.body.clientWidth;
    let height = 0;
    
    if(w <= 768){
      height= this.$refs.introduce.offsetHeight + 200;
    }else{
      height= this.$refs.introduce.offsetHeight;
    }

    if(height > h){
      this.isFixed = false
    }else{
      this.isFixed = true
    }

    this.isMobile = w <= 768
  }
}
</script>

