@media (max-width: 768px){
  .introduce-wrapper{
    .model-list{
      margin-bottom:37px;
    }
  }
}
@media (min-width: 768px){
  .introduce-wrapper{
    .model-list{
      width: 1411px;
      margin:42px auto 0 auto;
    }
  }
